import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileHttpUpListResponse } from "../../Model/Response/FileHttpUpListResponse";
import { FileHttpUpResponse } from "../../Model/Response/FileHttpUpResponse";
import { FileHttpUpViewModel } from '../../Model/ViewModel/FileHttpUpViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileHttpUpServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileHttpUpServicesProvider extends FileServicesProvider {
  private FileHttpUps: FileHttpUpListResponse;
  private FileHttpUp: FileHttpUpResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileHttpUp";
    console.log("Hello FileHttpUpServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileHttpUp?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileHttpUp/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileHttpUps = new FileHttpUpListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileHttpUps;
    });
  }



  public PostPutAsync(FileHttpUpViewModel:FileHttpUpViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
    {
      sub_url = "FileHttpUp";
      FileHttpUpViewModel.upload_seq=FileHttpUpViewModel.exec_file_seq*100+1;
    }
    else
    sub_url = "FileHttpUp/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileHttpUpViewModel.upload_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileHttpUpViewModel,mode).map(
      item => {
        this.FileHttpUp = new FileHttpUpResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileHttpUp;
      }
    );
  }
}



