import { SingleModelResponse } from "./SingleModelResponse";
import { FileHttpUpViewModel } from "../ViewModel/FileHttpUpViewModel";

export class FileHttpUpResponse extends SingleModelResponse<
  FileHttpUpViewModel
> {
  Model: FileHttpUpViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new FileHttpUpViewModel();
      this.Model.exec_file_seq = obj.Model.exec_file_seq;
      this.Model.exec_group = obj.Model.exec_group;
      this.Model.file_keyword = obj.Model.file_keyword;
      this.Model.file_min_length = obj.Model.file_min_length;
      this.Model.file_max_length = obj.Model.file_max_length;
      this.Model.is_active = obj.Model.is_active;
      this.Model.file_type_key = obj.Model.file_type_key;
      this.Model.file_proc_key = obj.Model.file_proc_key;
      this.Model.exec_seq = obj.Model.exec_seq;
      this.Model.can_rerun = obj.Model.can_rerun;
      this.Model.parameter_column_group = obj.Model.parameter_column_group;
      this.Model.creator = obj.Model.creator;
      this.Model.create_time = obj.Model.create_time;
      this.Model.modifier = obj.Model.modifier;
      this.Model.last_update_time = obj.Model.last_update_time;
      this.Model.note = (obj && obj.Model.note) || null;
      this.Model.file_type = obj.Model.file_type;
      this.Model.file_proc = obj.Model.file_proc;
      this.Model.is_active_desc = obj.Model.is_active_desc;
      switch (obj.Model.is_active) {
        case false: {
          this.Model.is_active_color = "danger";
          break;
        }

        default:
          this.Model.is_active_color = "active";
      }
      this.Model.is_fail_stop = obj.Model.is_fail_stop;
      this.Model.up_file_path_key = obj.Model.up_file_path_key;
      this.Model.bk_file_path_key = obj.Model.bk_file_path_key;
      this.Model.down_file_path_key = obj.Model.down_file_path_key;
      this.Model.up_file_path = obj.Model.up_file_path;
      this.Model.bk_file_path = obj.Model.bk_file_path;
      this.Model.down_file_path = obj.Model.down_file_path;

      this.Model.upload_seq = obj.Model.upload_seq;
      this.Model.function_name = obj.Model.function_name;
      this.Model.function_parameter =
        (obj && obj.Model.function_parameter) || null;
      this.Model.remote_conn_id = obj.Model.remote_conn_id;
      this.Model.parameter_column_group = obj.Model.parameter_column_group;
      this.Model.res_filename = obj.Model.res_filename;
      this.Model.upload_type_key = obj.Model.upload_type_key;
      this.Model.field_terminator = obj.Model.field_terminator;
      this.Model.upload_type = obj.Model.upload_type;
      this.Model.conn_string = obj.Model.conn_string;
      this.Model.url = obj.Model.url;
      this.Model.request_content=obj.Model.request_content;
      this.Model.response_content=obj.Model.response_content;
      this.Model.is_fail_stop=obj.Model.is_fail_stop;
      this.Model.is_processed_sql = obj.Model.is_processed_sql;
      if (this.Model.is_processed_sql === true) {
        this.Model.processed_sql_color = "danger";
      } else this.Model.processed_sql_color = "light";

      //  this.Model.is_processed_exec_group=obj.Model.is_processed_exec_group;

      //   if(this.Model.is_processed_exec_group===true)
      //   {
      //     this.Model.processed_exec_group_color="danger";
      //   }
      //   else
      //   this.Model.processed_exec_group_color="light";
    }
  }
}



